	module.exports = {"data":{"post":{"id":"/Users/thanhchungbui/Desktop/thanhchungbtcblog/content/posts/2017-11-15-security-check-list/index.md absPath of file >>> MarkdownRemark","html":"<p>長い時間プロジェクトと付き合って、リリースが近づくでしょう。とやっぱりセキュリティのことをそろそろ考えないといけないですね。ちょっと重要なポイントをメモしました。</p>\n<h1>1.チェックポイント</h1>\n<ul>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> ユーザー認証</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> アクセス制御</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> セッション管理</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> バリデーション</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> XSS</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> コマンドインジェクション</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> バッファオーバーフロー</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> 暗号化,秘密情報</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> エラーハンドリング</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> ロギング</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> サーバー設定</li>\n</ul>\n<h2>1.1.ユーザー認証</h2>\n<ul>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> ログイン失敗する際詳しメッセージが出さない</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> 指定回数でログイン失敗したらアカウントロックをかかる</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> アカウントロックをかかった際管理者にメール等で通知</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> パースワードの有効期限を実装</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> パースワード変更するには現在のパースワードが必要</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> 直接パースワードはDBに保存しない</li>\n</ul>\n<h2>1.2.権限制御</h2>\n<ul>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> 直接URLをアクセスは禁止する</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> 各アクションは実行する前に権限をチェックする\nボタンの表示、非表示等ではなくロジック実装である。</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> ブラウザの戻るボタンを要注意</li>\n</ul>\n<h2>1.3.セッション管理</h2>\n<ul>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> Cookieセンシティブデーターを持たない</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> SessionIdはユニックであり、サーバー側にてアサインするべきだ</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> SessionIdは推測しにくい</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> SessionIdはできればライフタイムが短く設定するべきだ</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> ログアウトする際SessionIdは廃棄するべき</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> CookieはHttpOnly,Secure属性設定するべき</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> パーマネントCookieよりセッションCookieを使用したほうが良い</li>\n</ul>\n<h2>1.4.バリデーション</h2>\n<ul>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> クライアントからのデータは信頼性がゼロとして必ずサーバーサイドバリデーション実装</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> HTML特殊文字、危険な文字等エスケープするべき</li>\n</ul>\n<h2>1.5.クロスサイトスクリプティング</h2>\n<ul>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> 動的な内容を表示する箇所はすべてHTMLエスケープするか確認</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> Query stringのデータは直接表示させない</li>\n</ul>\n<h2>1.6.コマンドインジェクション</h2>\n<ul>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> シェルコマンドが直接呼び出す際パラメータエスケープをするべきだ</li>\n</ul>\n<h2>1.7.SQLインジェクション</h2>\n<ul>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> SQLを組み立てる際ストリング結合ではなくすべてprepare statementを使用するべきだ\nー</li>\n</ul>\n<h2>1.8バッファオーバーフロー</h2>\n<ul>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> すべてのリクエストデータは最大値をバリデーションする</li>\n</ul>\n<h2>1.9暗号化,秘密情報</h2>\n<ul>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> 認証されたアルゴリズムを使用すること</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> 秘密鍵、DB情報等を持っている<code class=\"language-text\">.env</code>ファイルはh保護すること</li>\n</ul>\n<h2>1.10.エラーハンドリング</h2>\n<ul>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> エラーになった際エラー情報をユーザーに見せないこと</li>\n</ul>\n<h2>1.11.ロギング</h2>\n<ul>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> ログには日付、タイプ、詳細情報が出される</li>\n</ul>\n<h2>1.12.サーバー設定</h2>\n<ul>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> Debugモードオフする</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> すべてリクエストはHTTPSを介すること</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> Apacheサーバーの場合はApacheチェックリスト参照する</li>\n</ul>\n<h1>2. 各項目確認方法</h1>\n<h2>2.1.ユーザー認証</h2>\n<blockquote>\n<ul>\n<li>ログイン失敗する際詳しメッセージが出さない</li>\n<li>指定回数でログイン失敗したらアカウントロックをかかる</li>\n<li>パースワードの有効期限を実装</li>\n<li>パースワード変更するには現在のパースワードが必要</li>\n<li>直接パースワードはDBに保存しない</li>\n</ul>\n</blockquote>","fields":{"slug":"/2017-11-15-security-check-list/","prefix":""},"frontmatter":{"title":"ウェブのセキュリティチェックリスト","subTitle":"長い時間プロジェクトと付き合って、リリースが近づくでしょう。とやっぱりセキュリティのことをそろそろ考えないといけないですね。ちょっと重要なポイントをメモしました。","cover":null}},"author":{"id":"/Users/thanhchungbui/Desktop/thanhchungbtcblog/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p>My name is <strong>Thanh Chung</strong>, a Web Developer. I love reactjs and javascript stacks but it’s really hard. English is not my first language but this blog is aimed to improve my writing skill.</p>"},"footnote":{"id":"/Users/thanhchungbui/Desktop/thanhchungbtcblog/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>this blog is forked from   <a href=\"https://github.com/greglobinski/gatsby-starter-personal-blog\">gatsby-starter-personal-blog-starter-kit</a></li>\n<li>built by <a href=\"https://www.greglobinski.com\">greg lobinski</a></li>\n<li>with <a href=\"https://www.gatsbyjs.org/\">Gatsby</a> &#x26; <a href=\"https://reactjs.org\">React</a></li>\n</ul>"},"site":{"siteMetadata":{"facebook":{"appId":"238555440035867"}}}},"pathContext":{"slug":"/2017-11-15-security-check-list/"}}

